import dtPay from "../../common/dtpay.js";
export default{
	data:function(){
		return {
			orderid:0,
			paytype:"alipay"
		}
	},
	onLoad:function(ops){
		this.orderid=ops.orderid;
		 
	},
	methods:{
		setPayType:function(p){
			this.paytype=p;
			console.log(this.paytype)
		},
		pay:function(){
			var that=this;
			uni.request({
				url:that.app.apiHost+"/module.php?m=b2b_order&a=pay&ajax=1",
				data:{
					orderid:that.orderid,
					authcode:that.app.getAuthCode(),
					backurl:that.app.appRoot+"#/pages/b2b_order/success"
				},
				success:function(rs){
					dtPay.paytype=that.paytype;
					dtPay.pay({
						payurl:rs.data.data.payurl,
						orderno:rs.data.data.orderno
					});
				}
			})
			
		}
	}
}
